import React from 'react';
import Card from '../../componens/Card';
import classes from './Laptop_page.module.scss';
import axios from 'axios';

const arr = [
   {name: ' Acer Aspire 7 Charcoal', price: 31999, imgUrl:'assets/laptop/1.jpg'},
   {name: ' ASUS TUF Gaming F15 Bonfire Black', price: 34999, imgUrl:'assets/laptop/2.jpg'},
   {name: ' Lenovo IdeaPad 3 Grey', price: 25555, imgUrl:'assets/laptop/3.jpg'},
   {name: ' Apple MacBook Air 13" Silver', price: 42999, imgUrl:'assets/laptop/4.jpg'},
   {name: ' Acer Aspire 5 Pure Silver', price: 21999, imgUrl:'assets/laptop/5.jpg'},
   {name: ' ASUS Laptop Transparent Silver', price: 20999, imgUrl:'assets/laptop/6.jpg'},
   {name: ' Lenovo IdeaPad Gaming 3 Shadow Black', price: 29999, imgUrl:'assets/laptop/7.jpg'},
   {name: ' ASUS TUF Gaming A15 Graphite Black', price: 37999, imgUrl:'assets/laptop/8.jpg'},
   {name: ' Lenovo IdeaPad 3 Arctic Grey', price: 16999, imgUrl:'assets/laptop/9.jpg'},
   {name: ' Acer Aspire 7 Charcoal Black', price: 29999, imgUrl:'assets/laptop/10.jpg'},
   {name: ' ASUS Vivobook Go 15 Star Black', price: 14999, imgUrl:'assets/laptop/11.jpg'},
   {name: ' Asus ROG Strix G15 Eclipse Gray', price: 39999, imgUrl:'assets/laptop/12.jpg'},
   {name: ' Lenovo Legion 5 15ARH7H Storm Grey', price: 73999, imgUrl:'assets/laptop/13.jpg'},
   {name: ' Lenovo Yoga 7 Slate Grey', price: 47777, imgUrl:'assets/laptop/14.jpg'},
   {name: ' Apple MacBook Air 13" Custom Gold', price: 57999, imgUrl:'assets/laptop/15.jpg'},

];



const Laptop = ({searchValue, addElementToCart,addElementToFavourite, addCartItems, addFavouritItems}) => {

    React.useEffect(() => {
        axios.get('https://63d3d7d58d4e68c14eb3fb20.mockapi.io/cart').then((res)=>{
            addElementToCart(res.data)
        });
    }, []);

    React.useEffect(() => {
        axios.get('https://63d3d7d58d4e68c14eb3fb20.mockapi.io/favourite').then((res)=>{
            addElementToFavourite(res.data)
        });
    }, []);

    
    

    const onAddToCart = (obj) => {
        if (addCartItems.find((item) => item.name === obj.name)) {
                addElementToCart((prev) => prev.filter((item) => item.name !== obj.name))
        } else {
            axios.post('https://63d3d7d58d4e68c14eb3fb20.mockapi.io/cart', obj).then((res)=>{
                addElementToCart ([...addCartItems, res.data]);
            });
        }
    };

    const onAddToFavourite = (obj) => {
        if (addFavouritItems.find((item) => item.name === obj.name)) {
            addElementToFavourite((prev) => prev.filter((item) => item.name !== obj.name))
    } else {
        axios.post('https://63d3d7d58d4e68c14eb3fb20.mockapi.io/favourite', obj).then((res)=>{
            addElementToFavourite ([...addFavouritItems, res.data]);
        });
    }
    }
    

    return (

        


        <div className={classes.cards}>
           

                {arr.filter(arr => arr.name.toLowerCase().includes(searchValue.toLowerCase())).map((arr) => (
                    <Card

                        key={arr.name}
                        id={arr.id} 
                        name={arr.name} 
                        imgUrl={arr.imgUrl}
                        price={arr.price}
                        onClick={() => console.log(arr)}
                        onPlus={(obj)=> onAddToCart(obj)}
                        onFavourite={(obj)=> onAddToFavourite(obj)}
                       
                    />
                ))}
            
        </div>
    )
}

export default Laptop;