import React from 'react';
import Card from '../../componens/Card';
import classes from './Appliances_page.module.scss'
import axios from 'axios';

const arr = [
   {name: '   SAMSUNG WW80T3040BW', price: 14599, imgUrl:'assets/appliances/1.jpg'},
   {name: '  HOTPOINT ARISTON HI 5020 WEF', price: 15999, imgUrl:'assets/appliances/2.jpg'},
   {name: '  BEKO DF7439SX', price: 21555, imgUrl:'assets/appliances/3.jpg'},
   {name: ' BOSCH KGN39VL316', price: 25999, imgUrl:'assets/appliances/4.jpg'},
   {name: '  Samsung DW50R4040BB/WT', price: 9999, imgUrl:'assets/appliances/5.jpg'},
   {name: '  BEKO FSM52335DXDS', price: 11999, imgUrl:'assets/appliances/6.jpg'},
   {name: '   WHIRLPOOL WRBSS 6215UA', price: 11999, imgUrl:'assets/appliances/7.jpg'},
   {name: ' Whirlpool W7X 82O W', price: 19999, imgUrl:'assets/appliances/8.jpg'},
   {name: '  SAMSUNG DV90T6240LX/UA', price: 28999, imgUrl:'assets/appliances/9.jpg'},
   {name: '  INDESIT OMTWSE 61051 WK EU/UA', price: 10999, imgUrl:'assets/appliances/10.jpg'},
   {name: ' INDESIT INFC8 TI21X', price: 18999, imgUrl:'assets/appliances/11.jpg'},
   {name: '  HOTPOINT ARISTON HSIO 3O23 WFE', price: 14999, imgUrl:'assets/appliances/12.jpg'},
   {name: ' LG GW-B509SLKM', price: 25999, imgUrl:'assets/appliances/13.jpg'},
   {name: '  HANSA FCMW68020', price: 12777, imgUrl:'assets/appliances/14.jpg'},
   {name: '  HOTPOINT ARISTON 3T235WC', price: 16999, imgUrl:'assets/appliances/15.jpg'},

];



const Appliance = ({searchValue, addElementToCart,addElementToFavourite, addCartItems, addFavouritItems}) => {

    React.useEffect(() => {
        axios.get('https://63d3d7d58d4e68c14eb3fb20.mockapi.io/cart').then((res)=>{
            addElementToCart(res.data)
        });
    }, []);

    React.useEffect(() => {
        axios.get('https://63d3d7d58d4e68c14eb3fb20.mockapi.io/favourite').then((res)=>{
            addElementToFavourite(res.data)
        });
    }, []);

    
    

    const onAddToCart = (obj) => {
        if (addCartItems.find((item) => item.name === obj.name)) {
                addElementToCart((prev) => prev.filter((item) => item.name !== obj.name))
        } else {
            axios.post('https://63d3d7d58d4e68c14eb3fb20.mockapi.io/cart', obj).then((res)=>{
                addElementToCart ([...addCartItems, res.data]);
            });
        }
    };

    const onAddToFavourite = (obj) => {
        if (addFavouritItems.find((item) => item.name === obj.name)) {
            addElementToFavourite((prev) => prev.filter((item) => item.name !== obj.name))
    } else {
        axios.post('https://63d3d7d58d4e68c14eb3fb20.mockapi.io/favourite', obj).then((res)=>{
            addElementToFavourite ([...addFavouritItems, res.data]);
        });
    }
    }
    

    return (

        


        <div className={classes.cards}>
           

                {arr.filter(arr => arr.name.toLowerCase().includes(searchValue.toLowerCase())).map((arr) => (
                    <Card

                        key={arr.name}
                        id={arr.id} 
                        name={arr.name} 
                        imgUrl={arr.imgUrl}
                        price={arr.price}
                        onClick={() => console.log(arr)}
                        onPlus={(obj)=> onAddToCart(obj)}
                        onFavourite={(obj)=> onAddToFavourite(obj)}
                       
                    />
                ))}
            
        </div>
    )
}

export default Appliance;