import React from 'react';
import classes from './Order_page.module.scss'
import axios from 'axios';



const Order = ({ addElementToCart, items=[], index}) => {
    const onRemoveItem = (id) => {
        axios.delete(`https://63d3d7d58d4e68c14eb3fb20.mockapi.io/cart/${id}`).then((delres)=>{
            axios.get('https://63d3d7d58d4e68c14eb3fb20.mockapi.io/cart').then((res)=>{
                addElementToCart(res.data);
             });
        });
    }

    return (
        <div className={classes.overlay}>
            <div className={classes.drawer}>
                <div className={classes.top}>
                <h2> </h2>
                {/* <img className={classes.close} onClick={onCloseFavourite} src='/close_icon.svg' alt='close' width={25} height={25}/> */}
                </div>


            {
                    items.length > 0 ? <div className={classes.cards}>
                        {items.map((obj) => (
                           <div className={classes.card}>
                           <button className={classes.button_wish} >
                               <img onClick={() => onRemoveItem(obj.id)} src='/assets/like.png' alt='heart' width={20} height={20} />
                           </button>
               
                           <img src={obj.imgUrl} alt='laptop' width={169.5} height={208.02}/>
               
                           <h3>{obj.name}</h3>
                             
                           <div className={classes.price_block}>
                               <b>: {obj.price} .</b>
               
                             
                                   <img className={classes.button_cart}  src='/assets/add_plus.svg' alt='cart' width={27} height={25} />
                            
               
                           </div> 
                       </div>
                        ))}
                       
                    </div> : <div className={classes.empty_cart}>
                    <img  src='/assets/empty_cart.jpg' alt='cart-empty' width={350} height={350}/>
                    <p>   </p>
                </div>
                }

            </div>
        </div>
    )
}

export default Order;

