import React from 'react';
import classes from './Card.module.scss'
import axios from 'axios';


const Card = ({ id, imgUrl, price, name, onPlus, onFavourite, addElementToCart, onRemoveItem, addElementToFavourite }) => {
    const [isAdd, setIsAdd] = React.useState(false);
   
    const [isLiked, setIsLiked] = React.useState(false);

  

    const onClickPlus = () => {
        onPlus({id, imgUrl, price, name});
        setIsAdd(!isAdd);
    }

   

    const onClickLike = () => {
        onFavourite({id, imgUrl, price, name})
        setIsLiked(!isLiked)
    }

    const onRemovedItem = (obj) => {
        axios.delete(`https://63d3d7d58d4e68c14eb3fb20.mockapi.io/cart/${obj.id}`).then((delres)=>{
            axios.get('https://63d3d7d58d4e68c14eb3fb20.mockapi.io/cart').then((res)=>{
                addElementToCart(res.data);
             });
        });
    }

    

    return (
        <div className={classes.card}>
            <button className={classes.button_wish} >
                <img onClick={onClickLike} src={isLiked ? '/assets/like.png' : '/assets/dislike.png'} alt='heart' width={20} height={20} />
            </button>

            <img src={imgUrl} alt='laptop' width={169.5} height={208.02}/>

            <h3>{name}</h3>
              
            <div className={classes.price_block}>
                <b>: {price} .</b>

              
                    <img className={classes.button_cart} onClick={setIsAdd ? onClickPlus : onRemovedItem()} src={isAdd ?  '/assets/plus_active.svg' : '/assets/add_plus.svg'} alt='cart' width={27} height={25} />
             

            </div> 
        </div>
    )
}

export default Card;

