import React from 'react';
import classes from './Drawer.module.scss'
import axios from 'axios';
import { AppContext } from '../../App';

const Drawer = ({onCloseCart, addElementToCart, items=[], index}) => {
    const onRemoveItem = (id) => {
        axios.delete(`https://63d3d7d58d4e68c14eb3fb20.mockapi.io/cart/${id}`).then((delres)=>{
            axios.get('https://63d3d7d58d4e68c14eb3fb20.mockapi.io/cart').then((res)=>{
                addElementToCart(res.data);
             });
        });
    }

    const {cartItems} = React.useContext(AppContext);
    const TotalPrice = cartItems.reduce((sum, obj) => obj.price + sum, 0)


    

    return (
        <div className={classes.overlay}>
            <div className={classes.drawer}>
                <div className={classes.top}>
                <h2></h2>
                <img className={classes.close} onClick={onCloseCart} src='/close_icon.svg' alt='close' width={25} height={25}/>
                </div>

                {
                    items.length > 0 ? <div className={classes.cart_block}>
                    {items.map((obj)=>(
                    <ul key={obj.name} className={classes.cart_list}>
                        <li  className={classes.cart}>
                            <img src={obj.imgUrl} alt='laptop' width={100} height={100}/>
                            <h5>{obj.name}</h5>
                            <b>{obj.price} .</b>
                             <img  onClick={() => onRemoveItem(obj.id)} className={classes.close} src='/close_icon.svg' alt='close' width={25} height={25}/>
                        </li>   
                    </ul>))}
                </div> : <div className={classes.empty_cart}>
                    <img  src='/assets/empty_cart.jpg' alt='cart-empty' width={250} height={250}/>
                    <p> </p>
                </div>
                }

                <div className={classes.order_block}>
                 
                     <h3>{TotalPrice} .</h3>
                     <button className={classes.order_button}>
                        <h5> </h5>
                     </button>
                     
                </div>

            </div>
        </div>
    )
}

export default Drawer;