import React from 'react';
import classes from './Topbar.module.scss';
import Sidebar from '../Sidebar/Sidebar';
import { NavLink } from 'react-router-dom';
import { useState } from 'react';
import burgerIcon from './../../assets/topbar_logo/burger_icon.png';
import storeLogo from './../../assets/topbar_logo/store-logo.png';
import UA_logo from './../../assets/topbar_logo/UA.png';
import USA_logo from './../../assets/topbar_logo/USA.png';
import favourites from './../../assets/topbar_logo/favourites.png';
import cart from './../../assets/topbar_logo/cart.png';
import user from './../../assets/topbar_logo/user.png';


const Topbar = ({onChangeSearchInput, onClickCart, onClickFavourite}) => {

    const [menuActive, setMenuActive]= useState(false);

    return (
        <div className={classes.Topbar}>
            <div className={classes.top_block}>

           
            
                <div className='sidebar_button' style={{ display: 'flex', padding: 10 }}>
                    <button className="sb-button" onClick={() => setMenuActive(!menuActive)}><img src={burgerIcon} alt='burger_icon' width={30} height={30} /></button>
                   
                </div>
                <div className='logo'>
                    <img src={storeLogo} alt='logo' width={110} height={40}  />
                </div>
              
                    
                <input   onChange={onChangeSearchInput} type="search" placeholder=" ..."></input>
                       
                    
                
                <div className='change_language'>
                 <div className='change_language_items'>
                    <img src={UA_logo} alt='UA_logo' width={30} height={30} className={classes.lang_img} />
                    
                    <img src={USA_logo} alt='USA_logo' width={30} height={30} />
                 </div>
                </div>
                
                {/* <div className='wish_list' onClick={onClickFavourite}>
                    <NavLink to={'/liked'}><img src='/assets/dislike.png' alt='heart' width={27} height={27} /></NavLink>
                </div> */}
                <div className='overlay'>
                    <div className='wish_list' onClick={onClickFavourite}>
                        <NavLink to='/favourite' ><img src={favourites} alt='heart' width={27} height={27}  /></NavLink>
                    
                    </div>
                </div>

                {/* <div className='overlay'>
                    <div className='wish_list' onClick={onClickFavourite}>
                        <NavLink to={'/liked'}><img src='/assets/dislike.png' alt='heart' width={27} height={27} /></NavLink>
                    </div>
                </div> */}


                <div className='overlay'>
                    <div className='cart' onClick={onClickCart}>
                        <img src={cart} alt='cart' width={27} height={25} />
                    </div>
                </div>
                <div className='user'>
                    <NavLink to='/order' ><img src={user} alt='user' width={30} height={30} /></NavLink>
                
                </div>
            </div>

            <Sidebar
                            menuActive={menuActive}
                            setMenuActive={setMenuActive}
                        />
            
        </div>
        
    )
};

export default Topbar;