import React from "react";
import classes from './Sidebar.css';
import { NavLink } from "react-router-dom";

const Sidebar = ( {menuActive, setMenuActive}) => {
   
    return (
        <div className={menuActive ? 'menu active' : 'menu'} onClick={() => setMenuActive(false)} > 
          
            <div className="menu__content" onClick={e => e.stopPropagation()} >
            <nav className='nav_block'>
                <div className='nav_link'>
                    <NavLink to='/bonus' className={({ isActive }) => (isActive ? classes.act : '')}><h5>  </h5></NavLink>
                </div>
                <div className='nav_link'>
                    <NavLink to='/reviews'  className={({ isActive }) => (isActive ? classes.active : '')} ><h5> </h5></NavLink>
                </div>
                <div className='nav_link'>
                    <NavLink to='/comparison'  className={({ isActive }) => (isActive ? classes.active : '')} ><h5></h5></NavLink>
                </div>
                <div className='nav_link'>
                    <NavLink to='/delivery'  className={({ isActive }) => (isActive ? classes.active : '')} ><h5>  </h5></NavLink>
                </div>
               <div className='nav_link'>
                    <NavLink to='/insurance'  className={({ isActive }) => (isActive ? classes.active : '')} ><h5></h5></NavLink>
                </div>
               <div className='nav_link'>
                    <NavLink to='/credit'  className={({ isActive }) => (isActive ? classes.active : '')} ><h5></h5></NavLink>
                </div>
               <div className='nav_link'>
                    <NavLink to='/product_return'  className={({ isActive }) => (isActive ? classes.active : '')} ><h5> </h5></NavLink>
                </div>
               <div className='nav_link'>
                    <NavLink to='/contacts'  className={({ isActive }) => (isActive ? classes.active : '')} ><h5></h5></NavLink>
                </div>
                
            </nav>
            </div>
            
        </div>
    )
}

export default Sidebar;