import Carousel from 'react-bootstrap/Carousel';
import 'bootstrap/dist/css/bootstrap.min.css';
import spring from '../../slider/spring.png'
import summer from '../../slider/summer.png';
import autumn from '../../slider/autumn.png';
import winter from '../../slider/winter.png';
import classes from './Discount.module.scss'

function DiscountsSlider() {
  return (
    <Carousel className={classes.slider}>
      <Carousel.Item>
        <img
          className="d-block w-100"
          src={spring}
          alt="First slide"
        />
      </Carousel.Item>
      <Carousel.Item>
        <img
          className="d-block w-100"
          src={summer}
          alt="Second slide"
        />
      </Carousel.Item>
      <Carousel.Item>
        <img
          className="d-block w-100"
          src={autumn}
          alt="Third slide"
        />
      </Carousel.Item>

      <Carousel.Item>
        <img
          className="d-block w-100"
          src={winter}
          alt="Third slide"
        />
      </Carousel.Item>

    </Carousel>
  );
}

export default DiscountsSlider;