import React from 'react';
import Topbar from './componens/Topbar';
import Laptop from './product_list/Laptop/Laptop_page';
import Navlink from './componens/Navlink';
import { BrowserRouter, Route, Routes} from 'react-router-dom';
import Drawer from './componens/Drawer';
import {useState } from 'react';

import Appliance from './product_list/Appliance/Appliance_page';
import Phone from './product_list/Phone/Phone_page';
import DiscountsSlider from './componens/DiscoutsSlider/DiscountSlider';
import Favourite from './componens/Favourite';
import Order from './componens/Order/Order_page';



export const AppContext = React.createContext({});

function App() {
  const [cartItems, setCartItems] = useState([]);
  const [cartOpen, setCartOpen] = useState(false);

  
  const [favouriteItems, setFavouriteItems] = useState([]);
  const [favouritesOpen, setFaivouritesOpen] = useState(false);

  const [searchValue, setSearchValue] = useState('');
  const [onRemoveItem] = useState(false);

  

 
  

  return (
    <AppContext.Provider value={{cartItems, favouriteItems}} >
                <BrowserRouter>
              <div className='wrapper'>
                
                        <Topbar  
                              onChangeSearchInput={event => setSearchValue(event.target.value)} 
                              onClickCart={() => setCartOpen(true)}  
                              onClickFavourite={() => setFaivouritesOpen(true)} 
                        />

                        

                        

                       

                        {cartOpen ? <Drawer addElementToCart={setCartItems} items={cartItems} onCloseCart={() => setCartOpen(false)}/> : null}
                      
                      

                        <div className='content'>
                          <div className='leftbar'>
                            <div className='user_file'>
                              <img src='/assets/topbar_logo/user.png' alt='user' width={30} height={30} />
                              <p>SergiyPrytula@gmail.com</p>
                            </div>
                            <Navlink/>
                          </div>
                          <div className='rightbar'>

                          
                         

                              <DiscountsSlider/>
                          
                          
                          
                         
                            <Routes>
                            

                                  <Route 
                                    path='/*' 
                                    element={ <Phone
                                          searchValue={searchValue}
                                          addElementToCart={setCartItems}
                                          addCartItems={cartItems}
                                          onRemoveItem={onRemoveItem}
                                          addElementToFavourite={setFavouriteItems}
                                          addFavouritItems={favouriteItems}
                                        /> } 
                                    />

                                  

                                  <Route 
                                    path='/1' 
                                    element={ <Phone
                                          searchValue={searchValue}
                                          addElementToCart={setCartItems}
                                          addCartItems={cartItems}
                                          onRemoveItem={onRemoveItem}
                                          addElementToFavourite={setFavouriteItems}
                                          addFavouritItems={favouriteItems}
                                        /> } 
                                  />

                                  <Route 
                                    path='/2' 
                                    element={ <Laptop
                                          searchValue={searchValue}
                                          addElementToCart={setCartItems}
                                          addCartItems={cartItems}
                                          onRemoveItem={onRemoveItem}
                                          addElementToFavourite={setFavouriteItems}
                                          addFavouritItems={favouriteItems}
                                    
                                    /> } 
                                  />

                                  <Route 
                                    path='/3' 
                                    element={ <Appliance
                                          searchValue={searchValue}
                                          addElementToCart={setCartItems}
                                          addCartItems={cartItems}
                                          onRemoveItem={onRemoveItem}
                                          addElementToFavourite={setFavouriteItems}
                                          addFavouritItems={favouriteItems}
                                    /> } 
                                  />

                                  <Route 
                                    path='/favourite' 
                                    element={ <Favourite
                                      addElementToFavourite={setFavouriteItems} 
                                      items={favouriteItems} 
                                      onCloseFavourite={() => setFaivouritesOpen(false)}
                                      favouritesOpen={favouritesOpen}
                                    /> } 
                                  />

                                    <Route 
                                    path='/order' 
                                    element={ <Order
                                      addElementToFavourite={setFavouriteItems} 
                                      items={favouriteItems} 
                                      onCloseFavourite={() => setFaivouritesOpen(false)}
                                    /> } 
                                  />

                      </Routes>
                    
                    {/*  favouritesOpen */}
                          
                          </div>
                          
                        </div>
                       
              </div>
        </BrowserRouter>
    </AppContext.Provider>
    
  );
}

export default App;
