import React from 'react';
import Card from '../../componens/Card';
import classes from './Phone_page.module.scss';
import axios from 'axios';


const arr = [
    {name: '  Xiaomi Redmi Note 11', price: 7799, imgUrl:'assets/phone/1.jpg'},
    {name: '  Apple iPhone 13 128GB Pink', price: 31999, imgUrl:'assets/phone/2.jpg'},
    {name: '  Samsung Galaxy A33', price: 11999, imgUrl:'assets/phone/3.jpg'},
    {name: '  Samsung Galaxy S22 Ultra', price: 51099, imgUrl:'assets/phone/4.jpg'},
    {name: '  Apple iPhone 13 mini 128GB', price: 31499, imgUrl:'assets/phone/5.jpg'},
    {name: '  Nokia G10 3/32 GB Blue', price: 4499, imgUrl:'assets/phone/6.jpg'},
    {name: '  Infinix Note 12 Force Black', price: 8499, imgUrl:'assets/phone/7.jpg'},
    {name: '  Motorola E40 4/64GB Carbon Gray', price: 4999, imgUrl:'assets/phone/8.jpg'},
    {name: '  Apple iPhone SE 128GB', price: 24499, imgUrl:'assets/phone/9.jpg'},
    {name: '  Nokia G11 Plus 4/64GB Grey', price: 5499, imgUrl:'assets/phone/10.jpg'},
    {name: '  ZTE Blade A51 3/64GB Blue', price: 3999, imgUrl:'assets/phone/11.jpg'},
    {name: '  Samsung Galaxy Black', price: 5099, imgUrl:'assets/phone/12.jpg'},
    {name: '  Samsung Galaxy Fold 4 Phantom Black', price: 57499, imgUrl:'assets/phone/13.jpg'},
    {name: '  Apple iPhone 13 mini 256GB', price: 36499, imgUrl:'assets/phone/14.jpg'},
    {name: '  Poco M5s 6/128GB Yellow', price: 8499, imgUrl:'assets/phone/15.jpg'}
 
 ]



const Phone = ({searchValue, addElementToCart,addElementToFavourite, addCartItems, addFavouritItems}) => {

    React.useEffect(() => {
        axios.get('https://63d3d7d58d4e68c14eb3fb20.mockapi.io/cart').then((res)=>{
            addElementToCart(res.data)
        });
    }, []);

    React.useEffect(() => {
        axios.get('https://63d3d7d58d4e68c14eb3fb20.mockapi.io/favourite').then((res)=>{
            addElementToFavourite(res.data)
        });
    }, []);

    
    

    const onAddToCart = (obj) => {
        if (addCartItems.find((item) => item.name === obj.name)) {
                addElementToCart((prev) => prev.filter((item) => item.name !== obj.name))
        } else {
            axios.post('https://63d3d7d58d4e68c14eb3fb20.mockapi.io/cart', obj).then((res)=>{
                addElementToCart ([...addCartItems, res.data]);
            });
        }
    };

    const onAddToFavourite = (obj) => {
        if (addFavouritItems.find((item) => item.name === obj.name)) {
            addElementToFavourite((prev) => prev.filter((item) => item.name !== obj.name))
    } else {
        axios.post('https://63d3d7d58d4e68c14eb3fb20.mockapi.io/favourite', obj).then((res)=>{
            addElementToFavourite ([...addFavouritItems, res.data]);
        });
    }
    }

    

    return (

        


        <div className={classes.cards}>
           

                {arr.filter(arr => arr.name.toLowerCase().includes(searchValue.toLowerCase())).map((arr) => (
                    <Card

                        key={arr.name}
                        id={arr.id} 
                        name={arr.name} 
                        imgUrl={arr.imgUrl}
                        price={arr.price}
                        onClick={() => console.log(arr)}
                        onPlus={(obj)=> onAddToCart(obj)}
                        onFavourite={(obj)=> onAddToFavourite(obj)}
                        addElementToCart={(obj)=> addElementToCart(obj)}
                    />
                ))}
            
        </div>
    )
}

export default Phone;